import React, { useEffect, useState } from "react";
import {BrowserRouter as Router,Routes,Route,Link} from "react-router-dom";
import './App.css';
import { Layout, Menu, Button, Breadcrumb } from 'antd';
import { GoogleLogin, useGoogleLogin } from '@react-oauth/google';
import axios from "axios";
import { Header } from "antd/lib/layout/layout";
// import { CKEditor } from '@ckeditor/ckeditor5-react';
// import ClassicEditor from '@ckeditor/ckeditor5-build-classic';


function App() {
  const login = useGoogleLogin({
    onSuccess: async (codeResponse) => {
      const { code } = codeResponse
      console.log(codeResponse),
      await axios.post('http://localhost:5000/auth/google', {
        code,
      });
    },
    flow: 'auth-code',
  });

  const [time, setTime] = useState(new Date());
  const timer = setInterval(() => {
    setTime(new Date());
  }, 1000);

  useEffect(() => {
    clearInterval(timer);
  }, [timer]);
  
  return (
    <Layout className="layout">
    <h3> : {time.toLocaleTimeString()}</h3>
    <Button onClick={() => {
      login();
    }}></Button>
    <GoogleLogin
      onSuccess={credentialResponse => {
        console.log(credentialResponse);
      }}
      onError={() => {
        console.log('Login Failed');
      }}
    />;
    <Router>
          <Header>
            <Menu
              theme="dark"
              mode="horizontal"
              defaultSelectedKeys={["1"]}
              items={[{
                key: 1,
                label: <Link to="/">Home</Link>,
              }, {
                key: 2,
                label: <Link to="/resume"></Link>,
              },{
                key: 3,
                label: <Link to="/portfolio"></Link>,
              },{
                key: 4,
                label: <Link to="/Practice"></Link>
              },{
                key: 5,
                label: <Link to="/Board"></Link>
              }]}
            />
          </Header>
            <Layout.Content style={{ padding: '0 50px', height: "300px" }}>
            <div className="site-layout-content" style={{height: 200}}>
            <Routes>
              <Route path="/portfolio" element={<Portfolio />} />
              <Route path="/resume" element = {<Resume />} />
              <Route path="/" element={<Home />} />
              <Route path="/practice" element = {<Practice />} />
              <Route path="/board" element = {<Board />} />
            </Routes>
            </div>
          </Layout.Content>
        </Router>
        </Layout>
  );   }


